<template>
  <div class="quiz-page">
    <section v-if="!hasBank" class="empty card">
      <van-empty image="folder" description="请先导入题库">
        <van-button size="small" type="primary" @click="$router.push('/import')">
          去导入
        </van-button>
      </van-empty>
    </section>

    <template v-else>
      <section class="card level-summary">
        <div>
          <p class="level-summary__label">当前关卡</p>
          <p class="level-summary__title">{{ currentLevel?.title || '未选择' }}</p>
          <p v-if="currentLevel" class="level-summary__desc">
            题目 {{ currentLevel.start + 1 }} - {{ currentLevel.end }}
          </p>
        </div>
        <van-button size="small" plain type="primary" @click="openLevelPicker">
          切换关卡
        </van-button>
      </section>

      <section v-if="currentQuestion" class="card quiz-panel">
        <div class="quiz-panel__header">
          <div>
            <p class="level-title">{{ currentLevel?.title }}</p>
            <p class="progress-text">
              第 {{ questionProgress.current }}/{{ questionProgress.total }} 题
            </p>
          </div>
        </div>

        <question-card
          :question="currentQuestion"
          :value="answerValue"
          :disabled="currentQuestion.isAnswered"
          :show-result="showExplanation"
          @input="updateAnswer"
        />

        <div class="quiz-actions">
          <van-button
            type="default"
            size="small"
            :disabled="questionProgress.current === 1"
            @click="handlePrev"
          >
            上一题
          </van-button>
          <van-button
            type="primary"
            size="small"
            :loading="submitting"
            @click="handleSubmit"
          >
            {{ currentQuestion.isAnswered ? '重选答案' : '提交答案' }}
          </van-button>
          <van-button
            type="info"
            size="small"
            :disabled="!currentQuestion.isAnswered || showAnswerManually"
            plain
            @click="handleReveal"
          >
            查看解析
          </van-button>
        </div>

        <van-button
          v-if="questionProgress.current==questionProgress.total && canFinish"
          class="mt-12"
          block
          type="success"
          @click="handleFinish"
        >
          查看本关结果
        </van-button>
        <van-button
          v-else
          class="mt-12"
          block
          type="success"
          @click="handleNext"
        >
          下一题
        </van-button>
      </section>
    </template>

    <van-popup
      v-model="levelPickerVisible"
      position="bottom"
      round
      :style="{ height: '70vh' }"
    >
      <div class="level-popup">
        <div class="level-popup__header">
          <h3>选择关卡</h3>
          <van-icon name="cross" @click="closeLevelPicker" />
        </div>
        <level-navigator
          :levels="levels"
          :current-level-id="currentLevelId"
          :unlocked-levels="unlockedLevels"
          @select="handleSelectLevel"
        />
      </div>
    </van-popup>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import LevelNavigator from '@/components/common/LevelNavigator.vue';
import QuestionCard from '@/components/question/QuestionCard.vue';

export default {
  name: 'QuizPage',
  components: { LevelNavigator, QuestionCard },
  data() {
    return {
      answerValue: null,
      submitting: false,
      showAnswerManually: false,
      levelPickerVisible: false,
      autoNextTimer: null,
    };
  },
  computed: {
    ...mapGetters('questions', ['currentQuestion', 'questionProgress', 'levels']),
    ...mapGetters('questions', { hasBank: 'hasBank' }),
    ...mapState('questions', ['currentLevelId', 'currentQuestions', 'questionBank']),
    ...mapGetters('progress', ['unlockedLevels']),
    ...mapState('user', ['autoShowExplanation']),
    currentLevel() {
      return this.levels.find(level => level.id === this.currentLevelId) || null;
    },
    progressPercent() {
      if (!this.questionProgress.total) return 0;
      return Math.round(
        (this.questionProgress.current / this.questionProgress.total) * 100
      );
    },
    showExplanation() {
      if (!this.currentQuestion?.isAnswered) return false;
      return this.autoShowExplanation || this.showAnswerManually;
    },
    canFinish() {
      return (
        this.questionProgress.total > 0 &&
        this.currentQuestions.every(item => item.isAnswered)
      );
    },
  },
  watch: {
    currentQuestion: {
      immediate: true,
      handler(question) {
        console.log('question123', question);
        if (!question) {
          this.answerValue = null;
          return;
        }
        this.showAnswerManually = false;
        if (question.type === 'multiple') {
          this.answerValue = question.userAnswer ? [...question.userAnswer] : [];
        } else {
          this.answerValue = question.userAnswer || null;
        }
      },
    },
    levels: {
      immediate: true,
      handler() {
        this.bootstrapLevel();
      },
    },
  },
  mounted() {
    this.bootstrapLevel();
    console.log('currentQuestion', this.currentQuestion);
  },
  beforeDestroy() {
    if (this.autoNextTimer) {
      clearTimeout(this.autoNextTimer);
    }
  },
  methods: {
    ...mapActions('questions', [
      'loadLevelQuestions',
      'submitAnswer',
      'goNext',
      'goPrev',
      'finishLevel',
    ]),
    updateAnswer(value) {
      this.answerValue = value;
    },
    openLevelPicker() {
      this.levelPickerVisible = true;
    },
    closeLevelPicker() {
      this.levelPickerVisible = false;
    },
    handleSelectLevel(level) {
      this.loadLevelQuestions(level.id);
      this.closeLevelPicker();
    },
    handlePrev() {
      this.goPrev();
    },
    async handleSubmit() {
      if (!this.currentQuestion) return;
      if (this.currentQuestion.type === 'multiple' && !this.answerValue?.length) {
        this.$toast('请选择至少一个选项');
        return;
      }
      if (this.currentQuestion.type !== 'multiple' && !this.answerValue) {
        this.$toast('请选择一个答案');
        return;
      }
      this.submitting = true;
      try {
        const answer =
          this.currentQuestion.type === 'multiple'
            ? [...this.answerValue].sort()
            : this.answerValue;
        const isCorrect = await this.submitAnswer(answer);
        this.$toast(isCorrect ? '回答正确' : '再想想');
        if (!isCorrect) {
          if (this.currentQuestion.type === 'multiple') {
            this.answerValue = [];
          } else {
            this.answerValue = null;
          }
          
          this.currentQuestion.isAnswered = false;
        }
        if (!this.autoShowExplanation) {
          // this.showAnswerManually = true;
        }
        if (isCorrect) {
          this.scheduleAutoNext();
        }
      } catch (error) {
        this.$toast.fail('提交失败');
      } finally {
        this.submitting = false;
      }
    },
    handleReveal() {
      this.showAnswerManually = true;
    },
    async handleFinish() {
      if (!this.canFinish) {
        this.$toast('请完成本关所有题目后再查看结果');
        return;
      }
      await this.finishLevel();
      this.$router.push({ name: 'Result' });
    },
    handleNext() {
      this.goNext();
    },
    bootstrapLevel() {
      if (!this.hasBank || !this.levels.length) return;
      if (this.currentQuestions.length) return;
      const targetLevelId = this.currentLevelId || this.levels[0].id;
      if (targetLevelId) {
        this.loadLevelQuestions(targetLevelId);
      }
    },
    scheduleAutoNext() {
      if (this.autoNextTimer) {
        clearTimeout(this.autoNextTimer);
        this.autoNextTimer = null;
      }
      const isLastQuestion =
        this.questionProgress.current >= this.questionProgress.total;
      if (isLastQuestion) {
        return;
      }
      this.autoNextTimer = setTimeout(() => {
        this.goNext();
        this.autoNextTimer = null;
      }, 800);
    },
  },
};
</script>

<style lang="scss" scoped>
.quiz-page {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.level-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.level-summary__label {
  margin: 0;
  font-size: 12px;
  color: #94a3b8;
}

.level-summary__title {
  margin: 4px 0 0;
  font-size: 18px;
}

.level-summary__desc {
  margin: 4px 0 0;
  font-size: 13px;
  color: #6b7280;
}

.quiz-panel {
  margin-top: 8px;
  background: linear-gradient(180deg, #ffffff 0%, #eef4ff 120%);
  border: 1px solid rgba(59, 130, 246, 0.08);
}

.quiz-panel__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;
}

.level-title {
  margin: 0;
  font-size: 15px;
  color: #2563eb;
  font-weight: 600;
}

.progress-text {
  margin: 6px 0 0;
  color: #6b7280;
  font-size: 13px;
}

.quiz-panel__progress {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.progress-caption {
  font-size: 12px;
  color: #6b7280;
}

.quiz-actions {
  margin-top: 16px;
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.mt-12 {
  margin-top: 12px;
}

.empty {
  margin-top: 24px;
}

.level-popup {
  padding: 16px;
}

.level-popup__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.level-popup__header h3 {
  margin: 0;
}

@media (max-width: 414px) {
  .quiz-actions {
    flex-direction: column;
  }

  .quiz-panel__header {
    flex-direction: column;
    gap: 12px;
  }
}
</style>


