<template>
  <div v-if="question" class="question-card card">
    <div class="question-card__header">
      <div class="question-card__meta">
        <van-tag :type="typeTag" round plain>{{ typeLabel }}</van-tag>
        <span class="question-card__badge">ID：{{ displayId }}</span>
      </div>
      <p class="question-card__title">
        {{ question.question }}
      </p>
    </div>

    <div class="question-card__body">
      <van-radio-group
        v-if="isSingleType"
        class="option-group"
        :value="value"
        :disabled="disabled"
        @input="handleChange"
      >
        <van-radio
          v-for="option in question.options"
          :key="option.key"
          :name="option.key"
          class="option-item"
          icon-size="18"
        >
          <span class="option-key">{{ option.key }}.</span>
          <span class="option-label">{{ option.label }}</span>
        </van-radio>
      </van-radio-group>

      <van-checkbox-group
        v-else
        class="option-group"
        :value="value"
        :disabled="disabled"
        @input="handleChange"
      >
        <van-checkbox
          v-for="option in question.options"
          :key="option.key"
          :name="option.key"
          class="option-item"
          icon-size="18"
        >
          <span class="option-key">{{ option.key }}.</span>
          <span class="option-label">{{ option.label }}</span>
        </van-checkbox>
      </van-checkbox-group>
    </div>

    <div v-if="showResult" class="question-card__result">
      <div class="result-pill" :class="{ success: question.isCorrect }">
        {{ question.isCorrect ? '回答正确' : '回答错误' }}
      </div>
      <p class="correct-answer">
        正确答案：{{ formattedCorrectAnswer }}
      </p>
      <p v-if="question.explanation" class="explanation">
        解析：{{ question.explanation }}
      </p>
    </div>
  </div>
</template>

<script>
import { formatMultipleAnswer } from '@/utils/helper';

export default {
  name: 'QuestionCard',
  props: {
    question: {
      type: Object,
      required: true,
    },
    value: {
      type: [String, Array, null],
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    showResult: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isSingleType() {
      return this.question.type === 'single' || this.question.type === 'judge';
    },
    typeLabel() {
      if (this.question.type === 'multiple') return '多选';
      if (this.question.type === 'judge') return '判断';
      return '单选';
    },
    typeTag() {
      if (this.question.type === 'multiple') return 'warning';
      if (this.question.type === 'judge') return 'default';
      return 'primary';
    },
    formattedCorrectAnswer() {
      return Array.isArray(this.question.correctAnswer)
        ? formatMultipleAnswer(this.question.correctAnswer)
        : this.question.correctAnswer;
    },
    displayId() {
      return this.question.id || this.question.I || '未编号';
    },
  },
  methods: {
    handleChange(value) {
      this.$emit('input', value);
      this.$emit('update:value', value);
    },
  },
};
</script>

<style lang="scss" scoped>
.question-card__header {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.question-card__meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.question-card__badge {
  font-size: 12px;
  color: #94a3b8;
}

.question-card__title {
  font-weight: 700;
  line-height: 1.7;
  font-size: 18px;
  margin: 0;
  white-space: pre-wrap;
  word-break: break-word;
  color: #0f172a;
}

.question-card__body {
  margin-top: 14px;
}

.option-group {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.option-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  width: 100%;
  padding: 12px 14px;
  border-radius: 16px;
  border: 1px solid transparent;
  background: #f4f8ff;
  font-size: 14px;
  color: #1f2937;
  transition: all 0.2s ease;
}

.option-item::v-deep .van-radio__label,
.option-item::v-deep .van-checkbox__label {
  width: 100%;
  display: flex;
  align-items: flex-start;
}

.option-item::v-deep .van-radio__icon,
.option-item::v-deep .van-checkbox__icon {
  font-size: 18px;
}

.option-item:hover {
  border-color: rgba(59, 130, 246, 0.4);
  background: #e8f1ff;
}

.van-radio--checked .option-item,
.van-checkbox--checked .option-item {
  border-color: rgba(37, 99, 235, 0.5);
  background: rgba(59, 130, 246, 0.1);
  box-shadow: inset 0 0 0 1px rgba(37, 99, 235, 0.15);
}

.option-key {
  font-weight: 700;
  color: #2563eb;
  min-width: 24px;
}

.option-label {
  flex: 1;
  line-height: 1.6;
}

.question-card__result {
  margin-top: 18px;
  padding-top: 16px;
  border-top: 1px dashed #dbeafe;
}

.result-pill {
  display: inline-flex;
  padding: 6px 14px;
  border-radius: 999px;
  font-size: 13px;
  font-weight: 600;
  color: #ef4444;
  background: rgba(239, 68, 68, 0.08);
}

.result-pill.success {
  color: #10b981;
  background: rgba(16, 185, 129, 0.12);
}

.correct-answer,
.explanation {
  margin: 10px 0 0;
  font-size: 14px;
  color: #374151;
  line-height: 1.5;
}
</style>

