<template>
  <div class="level-nav card">
    <div class="level-nav__header">
      <h3 class="section-title">关卡选择</h3>
      <p v-if="levels.length" class="level-nav__meta">
        已解锁 {{ unlockedLevels.length }}/{{ levels.length }}
      </p>
    </div>

    <div v-if="levels.length" class="level-grid">
      <button
        v-for="level in levels"
        :key="level.id"
        class="level-cell"
        :class="{
          active: level.id === currentLevelId,
          locked: !isUnlocked(level.id),
        }"
        type="button"
        @click="handleSelect(level)"
      >
        <div class="level-cell__head">
          <span class="level-cell__title">{{ level.title }}</span>
          <van-icon
            :name="isUnlocked(level.id) ? 'success' : 'lock'"
            :color="isUnlocked(level.id) ? '#10b981' : '#f97316'"
          />
        </div>
        <p class="level-cell__range">
          题目 {{ level.start + 1 }} - {{ level.end }}
        </p>
        <p class="level-cell__status">
          {{ isUnlocked(level.id) ? '点击开始' : '完成前一关解锁' }}
        </p>
      </button>
    </div>

    <van-empty v-else description="请先导入题库" />
  </div>
</template>

<script>
export default {
  name: 'LevelNavigator',
  props: {
    levels: {
      type: Array,
      default: () => [],
    },
    currentLevelId: {
      type: String,
      default: '',
    },
    unlockedLevels: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    isUnlocked(levelId) {
      if (!this.unlockedLevels.length) {
        return levelId === this.levels[0]?.id;
      }
      return this.unlockedLevels.includes(levelId);
    },
    handleSelect(level) {
      if (!this.isUnlocked(level.id)) {
        this.$toast('先完成前一关卡即可解锁');
        return;
      }
      this.$emit('select', level);
    },
  },
};
</script>

<style lang="scss" scoped>
.level-nav__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.level-nav__meta {
  margin: 0;
  font-size: 13px;
  color: #64748b;
}

.level-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: 12px;
}

.level-cell {
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 16px;
  padding: 12px 14px;
  text-align: left;
  background: #f8fbff;
  transition: all 0.2s ease;
}

.level-cell__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

.level-cell__title {
  font-weight: 600;
  color: #0f172a;
}

.level-cell__range {
  margin: 0;
  font-size: 13px;
  color: #475569;
}

.level-cell__status {
  margin: 6px 0 0;
  font-size: 12px;
  color: #94a3b8;
}

.level-cell.active {
  border-color: rgba(37, 99, 235, 0.5);
  background: rgba(59, 130, 246, 0.12);
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.08);
}

.level-cell:not(.locked):hover {
  border-color: rgba(37, 99, 235, 0.4);
  transform: translateY(-2px);
}

.level-cell.locked {
  cursor: not-allowed;
  background: #fdf2e9;
  border-color: rgba(249, 115, 22, 0.4);
}
</style>

